/* global.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #e74c3c;
  --primary-dark: #c0392b;
  --secondary: #f39c12;
  --dark: #2c3e50;
  --light: #ecf0f1;
  --white: #ffffff;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  line-height: 1.6;
  color: var(--dark);
}

.page {
  padding-top: 80px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
