:root {
  --primary: #e74c3c;
  --primary-dark: #c0392b;
  --secondary: #f39c12;
  --dark: #2c3e50;
  --light: #ecf0f1;
  --white: #ffffff;
}

/* Menu Section */
.menu-section {
  padding: 6rem 2rem;
  max-width: 1400px;
  margin: 0 auto;
  background: #f8f9fa;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  margin-top: 2rem;
  border: 2px solid #e9ecef;
}

.menu-section h2 {
  text-align: center;
  font-size: clamp(2rem, 5vw, 3.5rem);
  color: var(--primary);
  margin-bottom: 3rem;
}

.pizza-showcase {
  text-align: center;
  margin-bottom: 4rem;
}

.hero-pizza {
  max-width: 400px;
  width: 100%;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.pizza-description {
  margin-top: 2rem;
  font-size: 1.2rem;
  color: #333;
  line-height: 1.8;
}

.size-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.size-card {
  background: white;
  padding: 2.5rem 2rem;
  border-radius: 20px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  text-align: center;
  transition: 0.3s;
}

.size-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.size-image {
  width: 100%;
  max-width: 200px;
  height: auto;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.size-card h3 {
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 0.5rem;
}

.size-desc { color: #999; margin-bottom: 0.5rem; }
.size-detail { color: #666; margin-bottom: 1.5rem; font-size: 0.9rem; }

.price {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--secondary);
  margin: 1.5rem 0;
}

.order-btn {
  width: 100%;
  padding: 1rem 2rem;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.3s;
  font-size: 1rem;
}

.order-btn:hover {
  background: var(--primary-dark);
  transform: translateY(-3px);
}

/* Cart */
.cart-toggle {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 70px;
  height: 70px;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 2rem;
  cursor: pointer;
  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
  z-index: 999;
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -5px;
  background: var(--secondary);
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  font-weight: 700;
}

.cart-drawer {
  position: fixed;
  top: 0;
  right: -450px;
  width: 350px;
  height: 100vh;
  background: white;
  box-shadow: -5px 0 30px rgba(0,0,0,0.3);
  z-index: 1001;
  transition: right 0.3s;
  display: flex;
  flex-direction: column;
}

.cart-drawer.open { right: 0; }

.cart-header {
  padding: 2rem;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close-cart {
  background: rgba(255,255,255,0.2);
  border: none;
  color: white;
  font-size: 2rem;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}

.cart-items {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
}

.cart-item {
  background: var(--light);
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
}

.ci-name { font-weight: 600; margin-bottom: 0.8rem; }

.ci-qty {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.qty-btn {
  width: 35px;
  height: 35px;
  border: none;
  background: var(--primary);
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 700;
}

.qty-input {
  width: 50px;
  padding: 0.5rem;
  text-align: center;
  border: 2px solid #ddd;
  border-radius: 8px;
}

.ci-price {
  font-weight: 700;
  color: var(--primary);
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}

.remove-item {
  background: none;
  border: none;
  color: var(--primary);
  cursor: pointer;
  text-decoration: underline;
}

.empty {
  text-align: center;
  padding: 4rem 2rem;
  color: #999;
}

.cart-footer {
  padding: 2rem;
  border-top: 2px solid var(--light);
  background: #fafafa;
}

.cart-total {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.total-amount { color: var(--primary); }

.cart-actions {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.control-btn {
  padding: 1.2rem;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

.control-btn.primary { background: var(--primary); color: white; }
.control-btn.secondary { background: var(--light); color: var(--dark); }

/* Responsive for menu/cart only */
@media (max-width: 768px) {
  .size-options { grid-template-columns: 1fr; }
  .cart-drawer { width: 100%; right: -100%; }
  .cart-toggle { width: 60px; height: 60px; }
}
