/* about.css */

/* About content */
.about-hero {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, rgba(255, 107, 107, 0.8), rgba(255, 165, 0, 0.8));
  color: white;
  text-align: center;
  padding: 2rem;
}

.about-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.about-content p {
  font-size: 1.2rem;
  opacity: 0.9;
}

.about-section {
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.about-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.about-card {
  background: rgba(255, 255, 255, 0.9);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.about-card h2 {
  color: #ff6b6b;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.about-card p {
  color: #666;
  line-height: 1.7;
}

@media (max-width: 768px) {
  .about-content h1 {
    font-size: 2rem;
  }

  .about-content p {
    font-size: 1rem;
  }

  .about-section {
    padding: 2rem 1rem;
  }

  .about-card {
    padding: 1.5rem;
  }
}
