/* 基本設定 */


/* リンクの色 */
a {
  color: #d35400; /* ピザらしいオレンジ色 */
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.shop-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

/* タイトルエリア */
.page-header {
  text-align: center;
  margin-bottom: 50px;
}

h1 {
  font-size: 28px;
  margin: 0 0 10px 0;
  padding: 40px;
  letter-spacing: 0.1em;
  font-weight: bold;
  color: #d35400;
}

.sub-title {
  font-size: 14px;
  color: #888;
  font-weight: normal;
}

/* 店舗情報の表組み */
.shop-info {
  border-top: 3px solid #d35400; /* 上の線を太くオレンジに */
  border-bottom: 1px solid #ddd;
}

.shop-row {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #eee; /* 区切り線は薄いグレー */
}

/* 左側の項目ラベル */
.shop-term {
  width: 30%;
  background-color: #fff5e6; /* 薄いオレンジの背景色 */
  padding: 20px;
  font-weight: bold;
  color: #333;
  box-sizing: border-box;
  display: flex;
  align-items: center; /* 上下中央揃え */
}

/* 右側の内容 */
.shop-desc {
  width: 70%;
  padding: 20px;
  margin: 0;
  box-sizing: border-box;
  background-color: #fff;
}

/* 地図エリアのスタイル（レスポンシブ対応） */
.map-area {
  margin: 15px 0;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9の比率で高さを確保 */
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.map-area iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

/* 細かい文字装飾 */
.shop-kana {
  font-size: 13px; 
  color: #666;
}

.shop-note {
  font-size: 13px; 
  color: #666;
  margin-left: 5px;
}

.tel-link {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  pointer-events: none; /* PCではクリック無効 */
}

/* サービスリスト */
.service-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 15px;
}

.service-list li {
  background-color: #eee;
  padding: 2px 10px;
  border-radius: 4px;
  font-size: 14px;
}

/* スマホ用レスポンシブ */
@media (max-width: 600px) {
  .shop-term, .shop-desc {
    width: 100%;
    display: block;
  }
  .shop-term {
    background-color: #fff; /* スマホでは白背景に戻す */
    border-left: 5px solid #d35400; /* 左にオレンジの線 */
    padding: 10px 15px;
    margin-top: 20px;
    color: #d35400;
  }
  .shop-desc {
    padding: 5px 15px 20px 15px;
  }
  
  /* スマホのみ電話リンクを有効に */
  .tel-link {
    color: #007bff;
    pointer-events: auto; 
    text-decoration: underline;
  }
  
  /* 最初の行の上マージンを消す */
  .shop-row:first-child .shop-term {
    margin-top: 0;
  }
}
/* Footer */
footer {
    background: #333;
    color: white;
    text-align: center;
    padding: 30px;
    margin-top: 2rem;
}

/* Responsive */
@media (max-width: 768px) {
    .pc-links {
        display: none;
    }
    
    .hamburger {
        display: flex;
    }
    
    .nav-cards {
        position: fixed;
        top: 70px;
        left: 0;
        width: 100%;
        background: white;
        flex-direction: column;
        padding: 1rem;
        box-shadow: 0 5px 15px rgba(0,0,0,0.1);
        z-index: 999;
    }
    
    .nav-cards.active {
        display: flex;
    }

    .mobile-logout {
        justify-content: space-between;
        padding: 1rem;
    }

    .mobile-logout-btn {
        background: #667eea;
        color: white;
        border: none;
        padding: 0.4rem 0.8rem;
        border-radius: 6px;
        cursor: pointer;
        font-size: 0.8rem;
    }
    
    .nav-card {
        padding: 1rem;
        border-bottom: 1px solid #eee;
        text-decoration: none;
        color: #333;
        display: flex;
        align-items: center;
        gap: 1rem;
    }
    
    .nav-card:hover {
        background: #f5f5f5;
    }
    
    .hero h1 {
        font-size: 2rem;
    }
    
    .hero-content {
        padding: 2rem;
        margin: 1rem;
    }
}