body {
  font-family: "Segoe UI", sans-serif;
  background: #fff7f2;
  margin: 0;
}

.btn-disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* メインコンテナ */
.container {
  max-width: 480px;
  margin: 40px auto;
  background: #ffffff;
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

/* タイトル */
h1 {
  text-align: center;
  color: #d84315;
  margin: 0 0 14px;
}

/* 現金のみ注意（ログイン/空カートの案内もここで使える） */
.notice {
  background: #fff3e0;
  border-left: 6px solid #ff9800;
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 20px;
  font-weight: 700;
  line-height: 1.5;
}

/* 注文内容 */
.order-summary {
  background: #fafafa;
  padding: 12px;
  border-radius: 12px;
  margin-bottom: 20px;
  border: 1px solid #eee;
}

.order-summary h3 {
  margin: 0 0 10px;
}

.order-item {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  font-size: 14px;
  margin-bottom: 6px;
  padding: 6px 0;
  border-bottom: 1px dashed #eee;
}

.order-item:last-child {
  border-bottom: none;
}

.total {
  font-weight: 800;
  text-align: right;
  margin-top: 10px;
  font-size: 15px;
}

/* フォーム */
.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: 700;
  margin-bottom: 6px;
  color: #333;
}

input,
textarea {
  width: 100%;
  padding: 12px 12px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 14px;
  outline: none;
  box-sizing: border-box;
  background: #fff;
  transition: border-color .15s ease, box-shadow .15s ease;
}

textarea {
  resize: none;
}

/* フォーカス時 */
input:focus,
textarea:focus {
  border-color: #ff9800;
  box-shadow: 0 0 0 3px rgba(255,152,0,0.18);
}

/* readonly */
input[readonly] {
  background: #f5f5f5;
}

/* date/time の見た目を少し整える */
input[type="date"],
input[type="time"] {
  padding: 12px;
}

/* ✅ 画像みたいな支払い方法ボックス */
.payment-box {
  margin: 14px 0 10px;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid #ffd8a8;
  background: #fff3e6;
  font-weight: 800;
}

/* ✅ ボタン横並び用（注文確定・戻る） */
.btn-row {
  display: flex;
  gap: 14px;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10px;
}

/* 注文ボタン */
.submit-btn {
  width: 100%;
  background: #d84315;
  color: white;
  border: none;
  padding: 14px;
  font-size: 16px;
  border-radius: 12px;
  cursor: pointer;
  transition: transform .05s ease, background .15s ease, opacity .15s ease;
}

.submit-btn:hover {
  background: #bf360c;
}

.submit-btn:active {
  transform: translateY(1px);
}

/* 横並び時は幅を自動にする */
.btn-row .submit-btn {
  width: auto;
  flex: 1;
  min-width: 180px;
}

/* ✅ 戻るボタン（aタグ用） */
.back-btn {
  display: inline-block;
  text-decoration: none;
  text-align: center;
  padding: 14px;
  border-radius: 12px;
  border: 1px solid #d7d7ff;
  background: #fff;
  color: #6b4eff;
  transition: opacity .15s ease, transform .05s ease;
  flex: 0.8;
  min-width: 140px;
  box-sizing: border-box;
}

.back-btn:hover {
  opacity: .9;
}

.back-btn:active {
  transform: translateY(1px);
}

/* ✅ 画像下の注意文 */
.form-note {
  margin-top: 10px;
  color: #666;
  font-size: 13px;
  line-height: 1.4;
}

/* スマホ微調整 */
@media (max-width: 520px) {
  .container {
    margin: 18px 12px;
    padding: 18px;
  }
  h1 {
    font-size: 20px;
  }
}
.payment-box{
  margin: 14px 0 10px;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid #ffd8a8;
  background: #fff3e6;
  font-weight: 700;
}

.btn-row{
  display:flex;
  gap:14px;
  align-items:center;
  flex-wrap:wrap;
  margin-top:10px;
}

.back-btn{
  display:inline-block;
  width:100%;
  max-width:220px;
  text-align:center;
  padding:14px;
  border-radius:12px;
  text-decoration:none;
  border:1px solid #d7d7ff;
  background:#fff;
  color:#6b4eff;
}
.input-error {
  border: 1px solid #d33 !important;
  outline: none;
}

.field-error {
  margin-top: 6px;
  font-size: 13px;
  color: #d33;
}

