/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Arial", sans-serif;
  line-height: 1.6;
  color: #333;
  background: linear-gradient(135deg, #667eea, #764ba2, #f093fb);
  min-height: 100vh;
}

/* Main content */
.login-hero {
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    135deg,
    rgba(102, 126, 234, 0.8),
    rgba(118, 75, 162, 0.8)
  );
  color: white;
  text-align: center;
  padding: 2rem;
}

.login-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.login-content p {
  font-size: 1.2rem;
  opacity: 0.9;
}

.login-section {
  background: linear-gradient(135deg, #667eea, #764ba2, #f093fb);
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

/* Tabs */
.form-tabs {
  display: flex;
  border-bottom: 2px solid #ddd;
  width: 100%;
  justify-content: center;
}

.tab-btn {
  padding: 1rem 2rem;
  background: none;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  color: #a09f9f;
  cursor: pointer;
  transition: all 0.3s ease;
  border-bottom: 3px solid transparent;
}

.tab-btn.active {
  /* color: #667eea; */
  color: rgb(210, 210, 210);
  border-bottom-color: #667eea;
}

.tab-btn:hover {
  color: #788ef3;
}

/* Forms */
.form-section {
  width: 100%;
  display: flex;
  justify-content: center;
}

.form-section .login-form,
.form-section .register-form {
  width: 100%;
  max-width: 400px;
}

.login-form,
.register-form {
  background: rgba(255, 255, 255, 0.95);
  padding: 3rem;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #333;
  font-weight: 500;
}

.form-group input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s;
}

.form-group input:focus {
  outline: none;
  border-color: #667eea;
}

.login-btn {
  width: 100%;
  padding: 0.75rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s;
}

.login-btn:hover {
  transform: translateY(-2px);
}

.form-links {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
}

.form-link {
  color: #667eea;
  text-decoration: none;
  font-size: 0.9rem;
}

.form-link:hover {
  text-decoration: underline;
}

/* Responsive */
@media (max-width: 768px) {
  .login-content h1 {
    font-size: 2rem;
  }

  .login-content p {
    font-size: 1rem;
  }

  .login-section {
    padding: 2rem 1rem;
  }

  .login-form,
  .register-form {
    padding: 2rem;
  }

  .form-links {
    flex-direction: column;
    gap: 1rem;
  }
}
